import React, { useState } from 'react';
import "./display.css"
import moment from "moment"
import momenttz from "moment-timezone"
import TimePicker from 'react-time-picker';

export const Buzy = (props) => {
    return (  <div className="buzy-container">{props.title}
    <div className="buzy-container-status-red">

    </div>
    </div>);
}

export const Free = () => {
    return (  <div>Free</div>);
}

export const Next = () => {
    return (  <div>Next</div>);
}

export const AddBooking = (props) => {
    const [value, onChange] = useState('10:00');
    /**
     *  <TimePicker
        onChange={onChange}
        value={value}
      />
     */
    return (  <div>
       
        <button onClick={()=>{
            if (props.onAdd){
                props.onAdd()
            }
        }}>Book now</button>
        </div>);
}
 
 
const organizer = (organizer) => {
    
    
    if (!organizer) return null
    if (!organizer.emailAddress) return null
    
    //if (props.room && booking.organizer.emailAddress.address===props.room.email) return null
    return organizer.emailAddress.name + " (" + organizer.emailAddress.address+")"
  }

  export const ViewBooking = (props) => {
    const tz = props.timezone ? props.timezone :"Europe/Berlin"
    function hhmm(time){
        var dateTime = moment.tz(time.dateTime,time.timeZone)
        //return moment.tz(dateTime,"Asia/Taipei").format("hh:mm")
        return moment(dateTime).tz(tz).format("HH:mm")
    }
    

    return (  <div style={{padding:"10px"}}>
        
        <div>{hhmm(props.start)} - {hhmm(props.end)}</div><div>
        {organizer(props.organizer)}</div>

        {props.onDelete && <button onClick={()=>{
            if (props.onDelete){
                props.onDelete(props.id)
            }
        }}>End now</button>}
        {props.onChange && <button style={{marginLeft:"10px"}} onClick={()=>{
            if (props.onChange){
                props.onChange(props.id)
            }
        }}>Edit end time</button>}
        </div>); 
}

