import React, { useState, useEffect } from "react";
import {
  Free,
  Buzy,
  Next,
  ViewBooking,
  AddBooking,
} from "../components/RoomDisplay";
import {
  getRoom,
  getRoomBookingsForToday,
  addBooking,
  deleteBooking,
} from "../logic/intranets-api";

import { useTimer } from "react-timer-hook";
import { getCurrentBookingStatus } from "../logic/bookings";
import moment from "moment-timezone";

import "./RoomDisplayPage.css";

import backgroupUpperRight from "../media/upperright.png";
import logo from "../media/logo.png";
const RoomDisplay = (props) => {
  const [room, setRoom] = useState({});
  const [isLoaded, setIsLoaded] = useState(false);
  const timeZone = unescape(props.timezone)
  useEffect(() => {
    async function run() {
      getRoomBookingsForToday(props.email).then((data) => {
        setRoomBookings(data.value);
        setIsLoaded(true);
      });
      getRoom(props.email).then((room) => {
        setRoom(room);
      });
    }
    run();
    var timerID = setInterval(() => run(), 10000);
    return function cleanup() {
      clearInterval(timerID);
    };
  }, [props.email]);

  const [error, setError] = useState("");
  const [roomBookings, setRoomBookings] = useState([]);
  var [
    isAvailable,
    availableFrom,
    availableTo,
    currentBooking,
    nextBooking,
    availableText,
  ] = getCurrentBookingStatus(roomBookings, moment());

  const CurrentStatus = () => {
    return (
      <div
        className={
          "room-status-block " + (isAvailable ? "room-available" : "room-buzy")
        }
        style={{ fontSize: "100px", textAlign: "center" }}
      >
        {isAvailable && (
          <div>
            <div className="status-block">
              <div className="status-heading">Available</div>
              <div className="status-subheading"></div>
            </div>
            <AddBooking
              onAdd={async (startDateTime, endDateTime) => {
                startDateTime = moment().format("YYYY-MM-DDTHH:mm:00");

                endDateTime = moment()
                  .add(15, "m")
                  .format("YYYY-MM-DDTHH:mm:00");

                await addBooking(
                  props.email,
                  startDateTime,
                  endDateTime,
                  timeZone
                );
                window.location.reload();
                //setRefresh(refresh + 1);
              }}
            ></AddBooking>
          </div>
        )}
        {!isAvailable && (
          <div>
            <ViewBooking
              onDelete={async (id) => {
                await deleteBooking(props.email, id);
                window.location.reload();
              }}
              onChange={async (id) => {
                //await deleteBooking(props.email, id);
                window.location.reload();
              }}
              {...currentBooking}
            />
          </div>
        )}
      </div>
    );
  };
  const DisplayHeader = () => {
    return <div className="displaytitle"> {room.displayName} </div>;
  };

  const ComingUp = () => {
    if (!nextBooking) return <div>No upcoming meetings</div>;
    return (
      <div>
        <ViewBooking {...nextBooking} />
      </div>
    );
  };

  const ListAllBookings = () => {
    return (
      <div>
        <div style={{ padding: "16px" }}></div>
        {roomBookings.map((booking) => {
          return (
            <ViewBooking
              onDelete={async (id) => {
                await deleteBooking(props.room.email, id);
                window.location.reload();
              }}
              {...booking}
            />
          );
        })}
      </div>
    );
  };

  const Clock = () => {
    var now = moment().format("HH:mm:ss");
    var day = moment().format("dddd, D MMMM, YYYY");
    const [date, setDate] = React.useState(new Date());

    //Replaces componentDidMount and componentWillUnmount
    React.useEffect(() => {
      var timerID = setInterval(() => tick(), 1000);
      return function cleanup() {
        clearInterval(timerID);
      };
    });

    function tick() {
      setDate(new Date());
    }

    return (
      <div className="clock">
        <div className="clock-hm">{now}</div>

        <div className="clock-day">{day}</div>
      </div>
    );
  };
  /**
   * <span>{days}</span>:<span>{hours}</span>:<span>{minutes}</span>:<span>{seconds}</span>
   */
  return (
    <div className="room-display">
      <img className="imgUpperRight" src={backgroupUpperRight} />

      <Clock />

      {isLoaded && (
        <div>
          <DisplayHeader />
          <CurrentStatus />
          <div className="next">
            <div className="nextHeader">NEXT</div>
            <ComingUp />
          </div>
        </div>
      )}
      <img className="imgLowerRight" src={logo} />
    </div>
  );
};

export default RoomDisplay;
